import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles(theme => ({
  root: {
    boxSizing: "border-box",
    padding: theme.spacing(3, 2),
    minHeight: "100%"
  },
  title: {
    fontWeight: 600,
    color: "#3f51b5"
  },
  subtitle: {
    fontWeight: 600,
    color: "#333"
  }
}));

export default function PaperSheet(props) {
  const classes = useStyles();

  return (
    <Paper className={classes.root}>
      <Typography component="p"><span className={classes.title}>Year:</span> {(props.movieInfo.Year) ? (props.movieInfo.Year) : "N/A"}</Typography>
      <Typography component="p"><span className={classes.title}>Directed by:</span> {(props.movieInfo.Director) ? (props.movieInfo.Director) : "N/A"}</Typography>
      <Typography component="p"><span className={classes.title}>Duration</span> {(props.movieInfo.Runtime) ? (props.movieInfo.Runtime) : "N/A"}</Typography>
      <Typography component="p"><span className={classes.title}>Release date:</span> {(props.movieInfo.Released) ? (props.movieInfo.Released) : "N/A"}</Typography>
      <Typography component="p"><span className={classes.title}>DVD Release:</span> {(props.movieInfo.DVD) ? (props.movieInfo.DVD) : "N/A"}</Typography>
      <Typography component="p"><span className={classes.title}>Production:</span> {(props.movieInfo.Production) ? (props.movieInfo.Production) : "N/A"}</Typography>
      <Typography component="p"><span className={classes.title}>Actors:</span> {(props.movieInfo.Actors) ? (props.movieInfo.Actors) : "N/A"}</Typography>
      <Typography component="p"><span className={classes.title}>Awards:</span> {(props.movieInfo.Awards) ? (props.movieInfo.Awards) : "N/A"}</Typography>
      <Typography component="p"><span className={classes.title}>Genre:</span>: {(props.movieInfo.Genre) ? (props.movieInfo.Genre) : "N/A"}</Typography>
      <Typography component="p"><span className={classes.title}>Language:</span> {(props.movieInfo.Language) ? (props.movieInfo.Language) : "N/A"}</Typography>
      <Typography component="p"><span className={classes.title}>Country:</span> {(props.movieInfo.Country) ? (props.movieInfo.Country) : "N/A"}</Typography>
      <Typography component="p"><span className={classes.title}>Box Office:</span> {(props.movieInfo.BoxOffice) ? (props.movieInfo.BoxOffice) : "N/A"}</Typography>
      <br/>
      <Typography component="p"><span className={classes.title}>Rating:</span></Typography>
      {(props.movieInfo.Ratings) ? (props.movieInfo.Ratings.map((movie, index) => (<Typography key={index} component="p"><span className={classes.subtitle}>{props.movieInfo.Ratings[index].Source}</span>: {props.movieInfo.Ratings[index].Value}</Typography>))) : "N/A"}
      <br/>
      <Typography component="p"><span className={classes.title}>IMDB Rating:</span> {(props.movieInfo.imdbRating) ? (props.movieInfo.imdbRating) : "N/A" }</Typography>
      <Typography component="p"><span className={classes.title}>IMDB Votes:</span> {(props.movieInfo.imdbVotes) ? (props.movieInfo.imdbVotes) : "N/A"}</Typography>
    </Paper>
  );
}