import React, { useEffect, useState } from "react";
import {useParams} from 'react-router-dom';
import MainMoviePaper from './MainMoviePaper';
import MoviePaperSecondary from "./MoviePaperSecondary";
import MoviePaperSynopsis from "./MoviePaperSynopsis";
import { Container, Grid, Box } from "@material-ui/core";

import { withStyles } from "@material-ui/core/styles";

const styles = {
  root: {
    flexGrow: 1
  }
};

const Detail = (movie) => {
    const { slug } = useParams();
    const [movieInfo, setMovieInfo] = useState([]);
  
    
    useEffect(() => {
      fetch(`https://www.omdbapi.com/?i=${slug}&apikey=4a3b711b&plot=full`)
        .then(response => response.json())
        .then(jsonResponse => {
              setMovieInfo(jsonResponse);
        });
    },[slug]);

    return (
      <Box mt={2}>
        <Container>
          <Grid spacing={2} container direction="row" justify="flex-start" alignItems="stretch">
            <Grid item xs={12} md={4}>
              <MainMoviePaper movieInfo={movieInfo} />
            </Grid>
            <Grid item xs={12} md={8}>
              <MoviePaperSecondary movieInfo={movieInfo} />
            </Grid>
            <Grid item xs={12} md={12}>
              <MoviePaperSynopsis movieInfo={movieInfo} />
            </Grid>
          </Grid>
        </Container>
      </Box>
    );
}

export default withStyles(styles)(Detail);
